.customHeader {
  color: #fff; /* Set your desired color */
}

.MuiDataGrid-row {
  background-color: white;
}

.MuiDataGrid-row:hover {
  background-color: rgb(
    9,
    245,
    40
  ) !important; /* Set the desired background color for the selected row */
}

.MuiDataGrid-row.selected {
  background-color: rgb(214, 20, 20) !important; /* Set the desired background color for the rows */
}
